@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}


.Page_image_01__hhNnf,
.Page_image_11__21rVS {
  width: 100%;
  height: 400px;
}

.Page_input_01__3AqTG {
  width: 100%;
  background-color: rgb(79, 175, 11);
  margin-top: -10px;
  padding-top: 20px;
  padding-bottom: 25px;
  color: white;
}

.Page_input__3jb1K {
  width: 60%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid rgb(110, 209, 91);
  border-radius: 10px;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
}

.Page_input__3jb1K:focus {
  border: 2px solid #3498db;
  outline: none;
}

h2 {
  width: 430px;
  margin-left: 100px;
}


.Page_line_02__2CpLS{
  height: 10px;
  background-color: rgb(79, 175, 11);
  width: 100%;
}

.Page_input02__1m4kg{
  width: 40%;
  padding: 5px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid rgb(110, 209, 91);
  border-radius: 10px;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
}

input[type="file"] {
  border: none;
  background-color: rgb(110, 209, 91);
  padding: 10px;
  margin-bottom: 10px;
  width: 100%;
}

label {
  font-weight: bold;
  color: #fffbfb;
  margin-bottom: 10px;
  margin-left: 10px;
  display: block;
}
.Page_div_09__WUws5{
  width: 100%;
  height: 20%;
  flex-direction: row;
  display: flex;
}
.Page_div_08__1Wo3g{
  width: 25%;
  background-color:rgb(79, 175, 11); 
  height: 20%;
  position: absolute;
  right: 0px;
}
.Page_div_11__2VyEF{
  width: 25%;
  background-color:rgb(79, 175, 11); 
  height: 13%;
  position: absolute;
  right: 0px;
}
.Page_div_07__3vTcS{
  height: 20%;
}

h4 {
  margin-left: 100px;
}

.Page_line_04__2SAE2{
  height: 40px;
  background-color: rgb(79, 175, 11);
  width: 100px;
  margin-top: 60px;
}

select {
  padding: 5px 0px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

/* Style the option elements */
option {
  background-color: rgb(79, 175, 11);
}

/* Style the dropdown arrow */
select:after {
  content: "\25BC";
  position: absolute;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

@media only screen and (max-width: 530px) {
  h2 {
    margin-left: 0;
    width: 200px;
  }
}

